class Receipt {
  id: number | undefined;
  payerName?: string;
  payerAddress?: string;
  referrerTo?: string;
  paidValue?: number;
  paidValueAsText?: string;
  date?: Date;
  payeeName?: string;
  payeeCpfOrRg?: string;
  payeeAddress?: string;

  get dateAsTimeStamp() {
    if (this.date) {
      const d: Date = new Date(this.date);
      return d.getTime();
    }
    return 9007199254740991;
  }

  get yearAndMonth() {
    if (this.date) {
      const d: Date = new Date(this.date);
      return `${d.getFullYear()}-${d.getMonth()}`
    }
    return undefined;
  }
}


export default Receipt;