import React, { useState, useEffect } from 'react';

import Receipt from '../../db/Receipt'
import Database from '../../db/Database';

interface NewEditReceiptPageProps {
  receiptId?: number,
  toClone?: boolean
  database: Database
}

const NewEditReceiptPage = (props: NewEditReceiptPageProps) => {

  const database = props.database;
  const [receipt, setReceipt] = useState<any>(new Receipt());
  const [screenMode, setScreenMode] = useState("create")

  useEffect(() => {
    if (props.receiptId) {
      if (props.toClone) {
        setScreenMode("clone");
      } else {
        setScreenMode("edit");
      }

      database.getReceipt(props.receiptId)
        .then((receipt) => {
          setReceipt(receipt);
        })
        .catch((exception) => {
          alert("Não foi possível carregar o recibo.");
          console.log(exception);
        });
    } else {
      setScreenMode("create");
    }
  }, [props.receiptId]);
  
  const cancel = function() {
    window.location.hash = "#receipt-list";
  };

  const done = async function() {
    const receiptModel = new Receipt();
    
    receiptModel.payerName = receipt.payerName;
    receiptModel.payerAddress = receipt.payerAddress;
    receiptModel.referrerTo = receipt.referrerTo;
    receiptModel.paidValue = receipt.paidValue;
    receiptModel.paidValueAsText = receipt.paidValueAsText;
    receiptModel.date = receipt.date;
    receiptModel.payeeName = receipt.payeeName;
    receiptModel.payeeCpfOrRg = receipt.payeeCpfOrRg;
    receiptModel.payeeAddress = receipt.payeeAddress;

    if (screenMode === "clone") {
      await database.addReceipt(receiptModel);
    }
    if (screenMode === "edit") {
      await database.updateReceipt(receiptModel, props.receiptId!);
    }
    if (screenMode === "create") {
      await database.addReceipt(receiptModel);
    }
    window.location.hash = "#receipt-list";
  };

  const handleChange = function(e: any) {
    const {name, value} = e.target;

    setReceipt((prevState: any) => {
      let receipt = { ...prevState }; 
      receipt[name] = value;
      return receipt;
    })
  };

  return (
    <div className="new-receipt">
      <header className="top-app-bar">
        <div className="top-app-bar__navigation">
          <a onClick={() => (window.history.back())}>
            <i className="fas fa-arrow-left"></i>
          </a>
        </div>

        <div className="top-app-bar__title">
          {screenMode==="create" && "Criando"}
          {screenMode==="edit" && "Editando"}
          {screenMode==="clone" && "Clonando"}
        </div>

        {screenMode=="create" && 
        <div className="top-app-bar__right-part">
          <a onClick={() => cancel()}>
            <i className="fas fa-times top-app-bar__icon"></i>
          </a>
        </div>}
      </header>

      <form className="receipt-form" name="receipt-form" 
        action="javascript:void(0);"
        noValidate >
        {/* <label htmlFor={"receiptNumber"}>Numero</label>
        <input 
          type="text" 
          name={"receiptNumber"} 
          placeholder="" 
          id={"receiptNumber"}
          /> */}

        <div className="receipt-payer">
          
          <h5>Recebido de</h5>

          <label htmlFor={"payerName"}>Recebi (emos) de</label>
          <input 
            className="u-full-width" 
            type="text"   
            name={"payerName"} 
            placeholder="" 
            id={"payerName"}
            value={receipt.payerName} 
            onChange={handleChange}
            autoFocus
            />

          <label htmlFor={"referrerTo" }>Referente</label>
          <input 
            className="u-full-width" 
            type="text"   
            name={"referrerTo" } 
            placeholder="" 
            id={"referrerTo" }
            value={receipt.referrerTo} 
            onChange={handleChange}
            />

          <label htmlFor={"paidValue"}>Valor</label>
          <input 
            className="u-full-width" 
            type="number"   
            name={"paidValue"} 
            placeholder="" 
            id={"paidValue"}
            value={receipt.paidValue} 
            onChange={handleChange}
            />
          </div>

          <label htmlFor={"payerAddress"}>Endereço</label>
          <input 
            className="u-full-width" 
            type="text"   
            name={"payerAddress"} 
            placeholder="" 
            id={"payerAddress"}
            value={receipt.payerAddress} 
            onChange={handleChange}
            />
          <div className="receipt-header">
          
          {/* <label htmlFor={"paidValueAsText"}>A importância de</label>
          <input 
            className="u-full-width" 
            type="text"   
            name={"paidValueAsText"} 
            placeholder="" 
            id={"paidValueAsText"}
            value={receipt.paidValueAsText} onChange={(e) => receipt.paidValueAsText = e.target.value}/> */}

        </div>

        <div className="receipt-date">
          <label htmlFor={"date"}>Data</label>
          <input 
            className="u-full-width" 
            type="date"  
            name={"date"} 
            placeholder="" 
            id={"date"}
            value={receipt.date} 
            onChange={handleChange}
            />
        </div>

        
        <div className="receipt-payee top-padding">
          <h5>Recebido por</h5>
          <div className="emitentCpf">
            <div>
              <label htmlFor={"payeeName"}>Emitente</label>
              <input 
                className="u-full-width" 
                type="text" 
                name={"payeeName"} 
                placeholder="" 
                id={"payeeName"}
                value={receipt.payeeName}
                onChange={handleChange}
                />
            </div>
            <div>
              <label htmlFor={"payeeCpfOrRg"}>CPF/RG</label>
              <input 
                className="u-full-width" 
                type="text"   
                name={"payeeCpfOrRg"} 
                placeholder="" 
                id={"payeeCpfOrRg"}
                value={receipt.payeeCpfOrRg} 
                onChange={handleChange}
                />
            </div>
          </div>

          <label htmlFor={"payeeAddress"}>Endereço</label>
          <input 
            className="u-full-width" 
            type="text"   
            name={"payeeAddress"} 
            placeholder="" 
            id={"payeeAddress"}
            value={receipt.payeeAddress} 
            onChange={handleChange}
            />

          {/* <label htmlFor={"payeeSignature"}>Assinatura</label>
          <input 
            className="u-full-width" 
            type="text"   
            name={"payeeSignature"} 
            placeholder="" 
            id={"payeeSignature"}
            /> */}
        </div>

        <div className="action-bar">
          <button onClick={() => done()}>
            Pronto
          </button>
        </div>
      </form>

    </div>
  );
}

export default NewEditReceiptPage;