import React, { useState, useEffect } from 'react';
import Receipt from '../../components/receipt/Receipt';
import ReceiptStub from '../../components/receipt/ReceiptStub';
import Database from '../../db/Database';

interface ReceiptsToPrintPageProps {
  database: Database
  receiptIds: [number]
}

const ReceiptsToPrintPage = (props: ReceiptsToPrintPageProps) => {
  const database = props.database;
  const [receipts, setReceipts] = useState<any[]>([]);

  useEffect(() => {
    async function fetchReceipts() {
      const receipts = await database.getReceipts(props.receiptIds);
      setReceipts(receipts);
    };
    fetchReceipts();
  }, []);

  return (
    <div className="receipt-to-print-list">

      <header className="top-app-bar no-printable">
        <div className="top-app-bar__navigation">
          <a onClick={() => (window.history.back())}>
            <i className="fas fa-arrow-left"></i>
          </a>
        </div>

        <div className="top-app-bar__title">
          Imprimir
        </div>
      </header>
      
      <div className="action-top-bar">
        <button className="button-primary no-printable" onClick={window.print}>
          Imprimir
        </button>
      </div>
      
      <div className="">
        <h2>Canhotos</h2>
        {receipts.map((receipt) => (
          <div key={receipt.id}>
            <ReceiptStub
              key={receipt.id}
              index={receipt.id}
              payee={receipt.payeeName}
              payeeCpfOrRg={receipt.payeeCpfOrRg}
              payeeAddress={receipt.payeeAddress}
              date={receipt.date}
              receipt={receipt}
            />
          </div>
        ))} 

        <h2>Recibos</h2>
        {receipts.map((receipt) => (
          <div key={receipt.id}>
            <Receipt
              key={receipt.id}
              index={receipt.id}
              payee={receipt.payeeName}
              payeeCpfOrRg={receipt.payeeCpfOrRg}
              payeeAddress={receipt.payeeAddress}
              date={receipt.date}
              receipt={receipt}
            />
          </div>
        ))} 
      </div>
    </div>
  );
}

export default ReceiptsToPrintPage;