const monthAsTextMap:any = {
  0: 'Janeiro',
  1: 'Fevereiro',
  2: 'Março',
  3: 'Abril',
  4: 'Maio',
  5: 'Junho',
  6: 'Julho',
  7: 'Agosto',
  8: 'Setembro',
  9: 'Outubro',
  10: 'Novembro',
  11: 'Dezembro'
}

// Formated as 2021-08-20
function friendlyYearAndMonthAsText(dateAsText: string) {
  if (!dateAsText) {
    return undefined;
  }

  const date = new Date(dateAsText);
  const year = date.getFullYear();
  const literalMonth = monthAsTextMap[date.getMonth()];
  if ((new Date()).getFullYear() === year) {
    return literalMonth;
  } else {
    return literalMonth + ' de ' + year;
  }
}

export default friendlyYearAndMonthAsText;